async generateissuedaddress() {

  this.loggedinuser = await this.termsuserservice.getloggedinuser();

if(this.loggedinuser.email == '') {
    this.presentAlert("", "", "User need to login. ");
    return;
}

if(!this.useraccount.kycid) {
    this.presentAlert("", "", "Login to use " );
 return;
}

    var data = {
      kycid: this.useraccount.kycid,
      email: this.loggedinuser.email,
      msgtype: "default",
      network: "testnet"
    }

 if(this.network == 'testnet') {
    data = {
      email: this.loggedinuser.email,
      kycid: this.useraccount.kycid,
      msgtype: "default",
      network: "testnet"
    };
 } else {
    data = {
      email: this.loggedinuser.email,
      kycid: this.useraccount.kycid,
      msgtype: "default",
      network: "livenet"
    };

 }

 try {

   this.blue011issue.issueuserrecord(data).then((data: any) => {
      if(data != null)
      {
        this.issued = data;
        this.yettodepositfund = true;

    this.issued.id = this.issued.address.substr(-5) ; 
    this.issued.status = 'issued'; 
    this.issued.balance = 0; 
    this.blue011issue.saveuserrecord(this.issued);
      }
      else {
    this.presentAlert("", "", "Error creating escrow " );
      }
   }, (err)=> {
    this.presentAlert("", "", "Error creating escrow " + err);
   });

  } catch (err) {

    this.presentAlert("Create escrow failed", "",  err);

  }
}


getaddressbalance() {

if(!this.issued.address) {
    this.presentAlert("", "", "Escrow address field empty " );
 return;
}

 this.blue011issue.getBalance(this.issued.address, this.network).then((data: any) => {
      if(data != null)
      {
        this.addressbalance = data;
      }
      else {
    this.presentAlert("", "", "Balance query failed" );
      }
   }, (err)=> {
    this.presentAlert("", "", "Balance query failed" + err );
   });
}

getwalletbalance() {

if(!this.useraccount.defaultwallet ) {
    this.presentAlert("", "", "Wallet address empty " );
 return;
}

 this.blue011issue.getBalance(this.useraccount.defaultwallet.address, this.network).then((data: any) => {
      if(data != null)
      {
        this.walletbalance = data;
        this.savewalletbalance(this.walletbalance);
      }
      else {
    this.presentAlert("", "", "Balance query failed " );
      }
   }, (err)=> {
    this.presentAlert("", "", "Balance query failed "+err );
   });
}

getescrowbalance(msg) {

if(msg.escrowaddress == null || msg.escrowaddress == ''  ) {
    this.presentAlert("", "", "Address empty " );
 return;
}
 
var prevstatus = msg.reqstatus ;

 this.blue011issue.getBalance(msg.escrowaddress, msg.network).then((data: any) => {
      if(data != null)
      {
       msg.balance =  data.balanceSat; // Number(data.balanceSat);
        if(data.totalReceivedSat > 0) {
          msg.reqstatus = "funded";
          msg.creditedamount = msg.balance;
        }

        if(data.totalSentSat > 0) {
          msg.reqstatus = "accepted";
          msg.acceptedamount = msg.balance;
        }
       this.tmpaddress = msg.escrowaddress;
       this.tmpunconfirmed = data.unconfirmedBalanceSat;

       msg.updatetype = "balanceupdate";
      this.validationrecord = msg;
       this.blue011issue.updateuserrecord(msg);
   //    this.loaduserrecords() ;
      }
   }, (err)=> {
    this.presentAlert("", "", "Balance query failed "+err );
   });
}


